/// <reference lib="dom.iterable"/>

import {html, render} from "lit";
import {v4 as uuid4} from "uuid";
import {AbstractPaymentClientProvider} from "@liberquack/utils/payment/client-providers/abstract-payment-client-provider.js";
import {AbstractPaymentClient} from "@liberquack/utils/payment/abstract-payment-client.js";
import {PaymentStripeClientProvider} from "@liberquack/utils/payment/client-providers/payment-stripe-client-provider.js";
import type {PaymentCalculatedCheckout, PaymentCheckoutExecution, PaymentPartialCheckout, PaymentUserData} from "@liberquack/utils/payment/types.js";
import {countries} from "@liberquack/utils/payment/countries.js";
import {dictionaryMapList} from "@liberquack/utils/_/dictionary.js";

export {html} from "lit";

const apiAddress = location.hostname === "localhost" ?
    "http://localhost:8505/payments-api" :
    "TODO: Need to define api"

export async function init(stripeKey?: string, baseApiAddress?: string) {
    const uiElm = document.createElement("div");

    const paymentClient = buildPaymentClient(stripeKey, baseApiAddress);
    paymentClient.init();

    async function renderCheckout(e: SubmitEvent) {
        e.preventDefault();
        const form = e.target as HTMLFormElement;
        const {price, currency} = Object.fromEntries(new FormData(form)) as any;
        const calculatedCheckout = await calculateCheckout(paymentClient, +price, currency);

        const checkoutElm = await paymentClient.buildUi(calculatedCheckout, {
            htmlTemplate: html`
                <h2 style="text-align: center">${currency} ${(+price).toFixed(2)}</h2>
                <stripe-submit-form>
                    <form @submit="${(e: SubmitEvent) => e.preventDefault()}">
                        <div>
                            <input class="form-field" type="email" placeholder="Email" name="email" required>
                        </div>
                        <div class="form-field">
                            <stripe-element .type="${"card"}" .opts="${{hidePostalCode: true}}"></stripe-element>
                        </div>
                        <div>
                            <stripe-submit-error></stripe-submit-error>
                        </div>
                        <div>
                            <input class="form-field" type="text" placeholder="Cardholder Name" name="name" required>
                        </div>
                        <div>
                            <select class="form-field" name="country" required>
                                <option value="" selected>Country</option>
                                ${dictionaryMapList(countries, (key, value) => html`
                                    <option value="${key}">${value}</option>
                                `)}
                            </select>
                        </div>
                        <div>
                            <input class="form-field" type="text" placeholder="Postal Code" name="postalCode" required>
                        </div>
                        <button class="button">Pay</button>
                    </form>
                </stripe-submit-form>
            `,
            onSuccess: async (completedCheckout) => {
                alert("Congratulations, Payment succeeded");
            },

            onError: (err, checkout) => {
                alert(err);
            }
        })

        render(checkoutElm, uiElm);
    }

    function renderPriceSelector() {
        const priceTemplate = html`
            <form @submit="${renderCheckout}">
                <div>
                    <input class="form-field" name="price" type="number" placeholder="0.00" required min="10" style="flex-grow: 1">
                    <select class="form-field" name="currency" required style="padding-left: 1ch; padding-right: 1ch;" required>
                        <option selected>USD</option>
                        <option>EUR</option>
                        <option>AUD</option>
                        <option>BRL</option>
                        <option>CAD</option>
                    </select>
                </div>
                <button class="button">Continue</button>
            </form>
        `

        render(priceTemplate, uiElm);
    }

    renderPriceSelector();

    return uiElm;
}

class PaymentClient<P extends AbstractPaymentClientProvider = any> extends AbstractPaymentClient<P> {

    constructor(providers: Array<() => P>, public apiBaseUrl: string) {
        super(providers);
    }

    protected async sendCheckout(checkout: PaymentCheckoutExecution) {
        const reqOpts = {body: JSON.stringify(checkout), method: "POST"};
        const [res, paymentUserData] = await fetch(this.apiBaseUrl + "/payments/3ds/checkout", reqOpts).then(async it => [it, await it.json()]);
        if (res.status >= 200 && res.status < 300) {
            return paymentUserData;
        } else {
            throw paymentUserData
        }
    }

    protected async sendCalculateCheckout(checkout: PaymentPartialCheckout): Promise<PaymentCalculatedCheckout> {
        const reqOpts = {body: JSON.stringify(checkout), method: "POST"};
        const [res, paymentUserData] = await fetch(this.apiBaseUrl + "/payments/3ds/calculate", reqOpts).then(async it => [it, await it.json()]);
        if (res.status >= 200 && res.status < 300) {
            return paymentUserData;
        } else {
            throw paymentUserData
        }
    }

    protected sendCancelCheckout(checkout: PaymentCheckoutExecution): Promise<PaymentUserData> {
        throw "Not implemented"
    }
}

function buildPaymentClient(stripeClientKey = "pk_test_51IG332FIx1iLyZGc71gYaVCUDKMPAFlRn49tyrzTqf3FYymriwKSJAnKbWyAK9c9WdIi3gn6O9Y8rOKc1tpSJGmQ00RU6pFiTC", apiBaseUrl = apiAddress) {
    const paymentClient = new PaymentClient([
        () => new PaymentStripeClientProvider(stripeClientKey)
    ], apiBaseUrl);
    return paymentClient;
}

async function calculateCheckout(paymentClient: ReturnType<typeof buildPaymentClient>, price: number, currency: string) {
    const calculatedCheckout = await paymentClient.calculateCheckout({
        provider: "stripe",
        items: [],
        inlineItems: [{
            price: price,
            currency: currency,
            type: "product",
            title: `${price} ${currency} - FINANCIAL SERVICES`,
            quantity: 1,
        }],
        userId: uuid4(),
    });

    return calculatedCheckout;
}